<template>
<div class="parent">
	<myHeader page='papers'></myHeader>
  <div class="container">
    <div class="modal fade" id="createExamModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
            <h4 class="modal-title" id="myModalLabel">创建试卷</h4>
          </div>
          <div class="modal-body">
            <form class="form-horizontal" id="createExamForm">
              <input type="hidden" name="userId" value="sdf_1e">
              <div class="form-group">
                <label for="examName" class="col-sm-2 control-label">试卷名</label>
                <div class="col-sm-10">
                  <input type="text" class="form-control" id="examName" placeholder="试卷名称" name="name">
                </div>
              </div>
              <div class="form-group">
                <label for="totalTime" class="col-sm-2 control-label">时长</label>
                <div class="col-sm-10">
                  <input type="number" class="form-control" id="totalTime" placeholder="考试时长" name="totalTime" min="0" step="10">
                </div>
              </div>
              <div class="form-group">
                <label for="mode" class="col-sm-2 control-label">模式</label>
                <div class="col-sm-10">
                  <select id="mode" name="mode" class="form-control">
                    <option value="1">整卷</option>
                    <option value="2">逐题</option>
                  </select>
                </div>
              </div>
            </form>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-primary" v-on:click="ensure">确定</button>
          </div>
        </div>
      </div>
    </div>
    <ul id="statusTabs" class="nav nav-pills">
      <li role="presentation" class="active"><a href="#tab1" data-toggle="tab">进行中({{ongoingNum}})</a></li>
      <li role="presentation"><a href="#tab2" data-toggle="tab">未开始({{readyNum}})</a></li>
      <li role="presentation"><a href="#tab3" data-toggle="tab">已结束({{endNum}})</a></li>
      <button type="button" class="btn" data-toggle="modal" data-target="#createExamModal">
        <span class="glyphicon glyphicon-plus" aria-hidden="true" ></span>&nbsp;创建试卷
      </button>
    </ul>
    <hr>
    <div class="tab-content">
      <div class="tab-pane fade in active" id="tab1">
        1111
      </div>
      <div class="tab-pane fade" id="tab2">
        <div class="paper_overview" v-for="paper in readyPapers">
          <p><a class="list_head">{{paper.name}}</a></p>
          <p>时长：{{paper.totalTime}}分钟</p>
          <p>模式：{{paper.mode == 1 ? '整卷' : '逐题'}}</p>
        </div>
      </div>
      <div class="tab-pane fade" id="tab3">
        333
      </div>
    </div>
  </div>
</div>
</template>
<script>
  import myHeader from '../components/Header'
  export default {
    name: 'papers',
    components: {
      myHeader
    },
    data(){
      return {
        ongoingPapers:[],
        readyPapers:[{name:'试卷1',totalTime:60,mode:1},{name:'paper2',totalTime:120,mode:2}],
        endPapers:[]
      }
    },
    computed:{
      ongoingNum(){
          return this.ongoingPapers.length;
      },
      readyNum(){
          return this.readyPapers.length;
      },
      endNum(){
          return this.endPapers.length;
      }
    },
    methods:{
      ensure:function () {
        /*$.ajax({
         url:'',
         type:'post',
         contentType:'application/json',
         data:JSON.stringify($('#createExamForm').serializeJson()),
         success:function(response){

         }
         })*/
        //TODO 改成交互，数据绑定
        console.log(JSON.stringify($('#createExamForm').serializeJson()));
        $('#createExamModal').hide();
        this.readyNum+=1;
      }
    },
    mounted(){
      $('#createExamModal').modal({backdrop:false,show:false,keyboard:false});
      $('#createExamModal').on('hidden.bs.modal', function (e) {
        $('form input').val('');
      });
      $('#statusTabs > li').click(function (e) {
        e.preventDefault();
        $('ul.nav > li').removeClass('active');
        $(this).addClass('active');
      });
      $('#statusTabs a').click(function (e) {
        e.preventDefault()
        $(this).tab('show')
      });
    }
  }
</script>
<style scoped>
  div.container{
    width:80%;
    padding: 15px;
    background-color: #eeeeee;
  }
  .nav-pills > li.active > a{
    background-color: #181818;
  }
  button.btn{
    margin:0 15px;
    float:left;
  }
  div.modal-footer .btn-primary{
    float:right;
  }
  form{
    margin:10px auto;
    width:80%;
  }
  div.tab-content{
    text-align:left;
  }
  div.paper_overview{
    border:1px solid #e6e6e6;
    padding:10px 15px;
    margin-bottom: 15px;
  }
  a.list_head{
    font-size:larger;
    font-weight: 600;
    text-decoration: none;
    cursor: pointer;
  }
</style>




