<template>
<div class="parent">
	<div class='login-form-box'>
	<h2>新用户注册</h2>
	<form class="form-horizontal" id="registerForm">
	  <div class="form-group">
	    <label for="userName" class="col-sm-3 control-label">用户名</label>
	    <div class="col-sm-9">
	      <input type="text" class="form-control" id="userName" placeholder="请输入你的用户名" name="name">
	    </div>
	  </div>
	  <div class="form-group">
	    <label for="password" class="col-sm-3 control-label">密码</label>
	    <div class="col-sm-9">
	      <input type="password" class="form-control" id="password" placeholder="请输入至少8个字符的密码" name="password">
	    </div>
	  </div>
    <div class="form-group">
      <label for="phone" class="col-sm-3 control-label">手机号</label>
      <div class="col-sm-9">
        <input type="text" class="form-control" id="phone" placeholder="请输入手机号码" name="tel">
      </div>
    </div>
	  <div class="form-group">
	    <div class="col-sm-offset-3 col-sm-9">
	      <button type="button" class="btn btn-primary" v-on:click="register">确认注册</button>
	      <p class='hint'>已有账号？直接<router-link to='login'>登录</router-link></p>
	    </div>
	  </div>
	</form>
	</div>
</div>
</template>

<script>
export default {
  name: 'Register',
  methods:{
    register:function(){
      let _this=this;
      $.ajax({
        url:'/api/user/register',
        type:'post',
        contentType:'application/json',
        data:JSON.stringify($('#registerForm').serializeJson()),
        success:function(response) {
          console.log(response);
          if (response.code === 200 && response.data !== null){
            var expires = new Date();
            expires.setTime(expires.getTime() + 30*60*1000);
            document.cookie = "tel="+response.data.tel+";role=user;userId=" + response.data.id +";path=/;expire=" +expires.toGMTString() + "";
            _this.$router.push('exam');
          }
        },
        error:function(a,b,c){
            console.log(a);
            console.log(b);
            console.log(c);
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
div.parent{
	background-image: url('../assets/image/loginbg.jpg');
	background-size: cover;
	display: flex;
}
</style>



