import Vue from 'vue'
import Router from 'vue-router'
import $ from 'jquery'
import Index from '@/view/index'
import Exam from '@/view/Exam'
import Papers from '@/view/Papers'
import Upload from '@/view/Upload'
import Login from '@/view/Login'
import Register from '@/view/Register'

import Manage from '@/view/Manage'

import Editor from '@/components/Editor'


Vue.use(Router)

export default new Router({
  routes: [
    {
    	path: '/',
    	name:'index',
    	component: Index
    },
    {
      path: '/exam',
      name:'exam',
      component: Exam
    },
    {
      path: '/papers',
      name: 'papers',
      component: Papers
    },
    {

      path: '/upload',
      name: 'upload',
      component: Upload
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/register',
      name: 'register',
      component: Register
    },
    {

      path: '/manage',
      name: 'manage',
      component: Manage
    },{
      path: '/editor',
      name: 'editor',
      component: Editor

    }

  ]
})



