<template>

  <div class="parent">
    <myHeader page='upload'></myHeader>
    <div class="container">
      <form id="uploadForm" enctype="multipart/form-data">
        <input id="file" type="file" name="file"style="border:none;border-radius:5px;position:relative;top:100px;left:370px;font-size:15px;width:300px;height:40px"/>
        <button id="upload" type="button" @click="savereport" style="border:none;border-radius:5px;position:relative;top:100px;right:50px;font-size:20px;width:300px;height:40px;BACKGROUND-COLOR: #4876FF;">上传</button>
      </form>
    </div>
  </div>
</template>

<script type="application/javascript">
  /*eslint-disable */
  import myHeader from '../components/Header'
  import $ from 'jquery'
  export default {
    name: 'papers',
    components: {
      myHeader
    },
    methods: {
        savereport:function(e) {
          var formData = new FormData($( "#uploadForm" )[0]);
          console.log(formData);
          let _this=this;
          let url='/api/question/upload?managerId=1';
          $.ajax({
            url: url ,
            type: 'POST',
            data: formData,
            async: false,
            cache: false,
            contentType: false,
            processData: false,
            success: function (returndata) {
              var result=eval(returndata);
              console.log(result.data);
              alert(result.data);
            },
            error: function (returndata) {
               var result=eval(returndata);
              console.log(result.data);
              alert(returndata.data);
            }
          });
        }

    }
  }
</script>

<style scoped>
  div.parent{
    background:url("../assets/image/bg2.png");
    background-size: cover;
    color:white;
  }
  div.container{
    margin-top:5%;
    text-align: center;
  }
  div.container .btn.btn-default{
    border-color: white;
    color:white;
    background-color: transparent;
    margin:1%;
  }
  div.form-group input{
    margin-left:auto;
    margin-right:auto;
  }
</style>



