<template>
<div class="parent">
	<div class='login-form-box'>
	<h2>用户登录</h2>
	<form class="form-horizontal" id="loginForm">
	  <div class="form-group">
	    <label for="userName" class="col-sm-3 control-label">账号</label>
	    <div class="col-sm-9">
	      <input type="text" class="form-control" id="userName" name="tel" placeholder="电话号码">
	    </div>
	  </div>
	  <div class="form-group">
	    <label for="password" class="col-sm-3 control-label">密码</label>
	    <div class="col-sm-9">
	      <input type="password" class="form-control" id="password" name="password" placeholder="密码">
	    </div>
	  </div>
    <div class="form-group">
      <label for="class" class="col-sm-3 control-label">登录类别</label>
      <div class="col-sm-9">
        <select class="form-control" id="class">
          <option value="manager">管理员</option>
          <option value="user">考生</option>
        </select>
      </div>
    </div>
	  <div class="form-group">
	    <div class="col-sm-offset-3 col-sm-9">
	      <button type="button" class="btn btn-primary" v-on:click="login">登录</button>
	      <p class='hint'>没有账号？请先<router-link to='register'>注册</router-link></p>
	    </div>
	  </div>
	</form>
	</div>
</div>
</template>

<script>
export default {
  name: 'Login',
  methods:{
    login:function(){
      let _this=this;
      let url='/api/'+$('#class').val()+'/login';
      $.ajax({
        url:url,
        type:'post',
        contentType:'application/json',
        data:JSON.stringify($('#loginForm').serializeJson()),
        success:function(response){
          console.log(response);
          if (response.code === 200 && response.data !== null){
            var expires = new Date();
            expires.setTime(expires.getTime() + 30*60*1000);
            document.cookie = "tel="+response.data.tel+";role="+$('#class').val()+";userId=" + response.data.id + ";path=/;expire=" +expires.toGMTString() + "";
            _this.$router.push('/exam');
          }
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
div.parent{
	background-image: url('../assets/image/loginbg.jpg');
	background-size: cover;
	display: flex;
}

</style>



