<template>
  <div class="parent">
    <nav class="nav navbar-default">
      <div class="container-fluid">
        <div class="navbar-header">
          <a class="navbar-brand" href="#"><img src="../assets/image/brand1.jpg"></a>
        </div>
      </div>
    </nav>
    <div class="wrapper">
    <div class="left-side">
      <div id="question_description">
        <h2>题目描述：</h2>
        <div v-html="question.description"></div>
      </div>
      <div id="console">
        <p class="hint">console</p>
      </div>
    </div>
    <div class="right-side">
      <div class="opts">
      <select class="form-control" v-model="mode">
        <option value="javascript">javascript</option>
        <option value="text/x-java">java</option>
        <option value="python">python</option>
        <option value="text/x-c++src">C++</option>
      </select>
      <select class="form-control" v-model="theme">
        <option value="default">default</option>
        <option value="monokai">monokai</option>
        <option value="eclipse">eclipse</option>
      </select>
      <button type="button" class="btn btn-primary pull-right">提交运行</button>

      </div>
      <codemirror :value="code" :options="options" ></codemirror>
    </div>
    </div>
  </div>
</template>
<script>
  //TODO 支持的语言和主题还可以增加
  import { codemirror } from 'vue-codemirror-lite'
  require('codemirror/mode/javascript/javascript');
  require('codemirror/mode/clike/clike');
  require('codemirror/mode/python/python');
  require('codemirror/addon/hint/show-hint.js');
  require('codemirror/addon/hint/show-hint.css');
  require('codemirror/addon/hint/javascript-hint.js');
  require('codemirror/theme/monokai.css');
  require('codemirror/theme/eclipse.css');

  let codes={
      'javascript':'var str = "hello world";',
      'text/x-java': 'String str = "hello world";',
      'python': 'str = "hello world"',
      'text/x-c++src': 'char* str= "hello world";'
  };
  export default {
    name: 'editor',
    components:{
        codemirror
    },
    data () {
      return {
        mode:'javascript',
        theme:'default',
        question:{
            description:"<p>输入一个整数，将其转换成八进制数输出。</p>" +
            "<p><b>输入描述</b>:输入包括一个整数N(0<=N<=100000)。</p><p><b>输出描述</b>:可能有多组测试数据，对于每组数据，输出N的八进制表示数。</p>"+
            "<p><b>输入描述</b>:输入包括一个整数N(0<=N<=100000)。</p><p><b>输出描述</b>:可能有多组测试数据，对于每组数据，输出N的八进制表示数。</p>"+
            "<p><b>输入描述</b>:输入包括一个整数N(0<=N<=100000)。</p><p><b>输出描述</b>:可能有多组测试数据，对于每组数据，输出N的八进制表示数。</p>"+
            "<p><b>输入描述</b>:输入包括一个整数N(0<=N<=100000)。</p><p><b>输出描述</b>:可能有多组测试数据，对于每组数据，输出N的八进制表示数。</p>"+
            "<p><b>输入描述</b>:输入包括一个整数N(0<=N<=100000)。</p><p><b>输出描述</b>:可能有多组测试数据，对于每组数据，输出N的八进制表示数。</p>"+
            "<p><b>输入描述</b>:输入包括一个整数N(0<=N<=100000)。</p><p><b>输出描述</b>:可能有多组测试数据，对于每组数据，输出N的八进制表示数。</p>"+
            "<p><b>输入描述</b>:输入包括一个整数N(0<=N<=100000)。</p><p><b>输出描述</b>:可能有多组测试数据，对于每组数据，输出N的八进制表示数。</p>"

        }
      }
    },
    computed: {
      code: function () {
        return codes[this.mode];
      },
      options: function () {
        return {
          mode: this.mode,
          theme: this.theme,
          tabSize: 2,
          lineNumbers: true,
          lineWrapping: true,
          extraKeys: {'Ctrl-Space': 'autocomplete'}
        }
      }
    }
  }
</script>
<style scoped>
  .parent{
    background-image: url("../assets/image/bg2.png");
  }
  .opts{
    text-align: left;
  }
  .wrapper{
    padding:40px 20px;
    width:100%;
    height:90%;
  }
  .left-side{
    position: relative;
    display: inline-block;
    float: left;
    width: 28%;
    margin-left:2%;
    margin-right: 1.5%;
    height:100%;
  }
  .right-side{
    display: inline-block;
    position:relative;
    float:left;
    margin-left:1.5%;
    margin-right:2%;
    width: 65%;
    height: 100%;
  }
  select.form-control{
    display:inline-block;
    width:200px;
  }
  #question_description{
    text-align: left;
    color:white;
    position: absolute;
    top:0;
    bottom:300px;
    border:2px solid #86a7ab;
    padding:10px 20px;
    background-color: transparent;
    overflow-y: auto;
  }
  #question_description h2{
    margin-top: 0;
    margin-bottom:20px;
  }
  #console{
    background-color: #0f0f0f;
    color:white;
    text-align: left;
    height:260px;
    position: absolute;
    bottom:0;
    left:0;
    right:0;
  }
  p.hint{
    background-color:rgba(134, 167, 171,0.4);
    padding:2px 10px;
  }
  ::-webkit-scrollbar
  {
    width: 12px;
    background-color: #2e4d50;
  }
  ::-webkit-scrollbar-track
  {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    border-radius: 10px;
    background-color: #2e4d50;
  }
  ::-webkit-scrollbar-thumb
  {
/*    background-image: -webkit-gradient(linear, left bottom, left top, color-stop(0.44, rgb(122,153,217)), color-stop(0.72, rgb(73,125,189)), color-stop(0.86, rgb(28,58,148)));*/
    border-radius: 10px;
    background-color: #86a7ab;
    border: 2px solid #555555;
  }
</style>



