<template>
 <nav class="navbar navbar-default">
  <div class="container-fluid">
    <div class="navbar-header">
      <a class="navbar-brand" href="#"><img src="../assets/image/brand1.jpg"></a>
    </div>
    <ul class="nav navbar-nav">
      <li v-bind:class="{'activated': page=='exam'}"><router-link href="javascript:void(0)" to='exam'>考试</router-link></li>
      <li v-bind:class="{'activated': page=='papers'}"><router-link href="javascript:void(0)" to='papers'>试题库</router-link></li>
      <li v-bind:class="{'activated': page=='upload'}" v-if="manager"><router-link href="javascript:void(0)" to='upload'>上传试题</router-link></li>
      <li v-bind:class="{'activated': page=='manage'}" v-if="manager"><router-link href="javascript:void(0)" to='manage'>管理试题</router-link></li>

    </ul>
    <ul class="nav navbar-nav navbar-right">
      <li class="dropdown" id="personal"><a href="#" class="dropdown-toggle" data-toggle="dropdown" role="button" aria-haspopup="true" aria-expanded="false">{{tel}}<span class="caret"></span></a>
        <ul class="dropdown-menu">
          <li><a href="#">个人信息</a></li>
          <li><router-link to="exam">我的试卷</router-link></li>
        </ul>
      </li>
    </ul>
  </div>
</nav>
</template>

<script>
export default {
  name: 'myHeader',
  props: ['page'],
  data () {
    return {
      tel: '',
      manager:false
    }
  },
  mounted(){
    let rolePatt=/role=(\w+)/;
    let userPatt=/tel=(\w+)/;
    let role=rolePatt.exec(document.cookie)[1];
    let tel=userPatt.exec(document.cookie)[1];
    this.tel=tel;
    if(role==='manager') {
      this.manager = true;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  nav.navbar.navbar-default{
    border-radius:0;
  }
  nav.navbar.navbar-default a{
    color:white;
  }
  .container-fluid{
    width:80%;
  }
  .navbar-header{
    margin:0 15px;
  }
  li.activated {
    background-color: #4c4c4c;
  }
  #personal ul.dropdown-menu li a{
    color:#4c4c4c;
  }
</style>



